import { useContext, useId } from 'react'
import { useParams } from 'react-router'
import { useFetching } from '../hooks/useFetching'
import { AuthContext } from '../context'
import Button from '../components/ui/Button'
import ResultService from '../api/ResultService'
import { Link, useNavigate } from 'react-router-dom'
import Input from '../components/ui/Input'

const Question = ({ questions, disciplineAndTeacher }) => {
  const { studentId, moderatorId } = useContext(AuthContext) 

  const params = useParams()
  const qNumber = params.number
  const qId = questions.find(q => q.number == qNumber).id

  const getQuestionTextById = (qId) => {
    return questions.find(q => q.id == qId)?.text
  }

  const redirect = useNavigate()

  const saveAnswers = () => {
    const results = []

    const discTeacherElem = document.querySelectorAll(".question__table tbody tr")
    for (let i = 0; i < discTeacherElem.length; i++) {
      let result = {
        id: 0,
        studentId: studentId,
        teacherId: parseInt(discTeacherElem[i].dataset.teacherid),
        disciplineId: parseInt(discTeacherElem[i].dataset.disciplineid),
        questionId: qId,
        moderatorId: moderatorId    
      }

      const answerChecked = discTeacherElem[i].querySelector("input[type=radio]:checked")
      if (answerChecked) {
        result.answerId = parseInt(answerChecked.closest("label").dataset.answerid)
        result.commentToAnswer = null
      } else {
        const answerComment = discTeacherElem[i].querySelector("input[type=text]")
        if (answerComment) {
          if (answerComment.value.trim().length > 0) {
            result.commentToAnswer = answerComment.value.trim()
            result.answerId = null
          } else {
            alert("      ")
            return
          }
        } else {
          alert("      ")
          return
        }
      }
      
      results.push(result)
    }
    createResults(results)
  }

  const [createResults, isResultsLoading, resultsErr] = useFetching(async (results) => {
    const response = await ResultService.createResults(results)
    redirect(`/student/question/${parseInt(qNumber) + 1}`, {
      state: { questions: questions, disciplineAndTeacher: disciplineAndTeacher }
    })
  })

  const resetRadioBtn = (name) => {
    const checkedEl = document.querySelector(`input[name="${name}"]:checked`)
    if (checkedEl) {
      checkedEl.checked = false
    }
  }

  const resetInputVaule = (inputId) => {
    const input = document.querySelector(`#${inputId}`)
    if (input) {
      input.value = ""
    }
  }

  const generateKey = prefix => `${prefix}-${new Date().getTime()}`;

  return (
    <div className='question'>
        <div className="container">
            <h2 className="question__title title">
                {
                  qNumber + ". " + getQuestionTextById(qId)
                }
            </h2>
            <div className="question__table">
                    {
                      <table>
                        <thead>
                          <tr>
                            <th>  </th>
                            <th> </th>
                          </tr>
                        </thead>
                        <tbody>
                        {
                          disciplineAndTeacher.map(item => {
                            return (
                              <tr key={item.id} data-teacherid={item.teachers.id} data-disciplineid={item.id}>
                                <td>{`${item.name} (${item.teachers.fio})` }</td>
                                <td>
                                    {
                                      questions.find(q => q.id == qId).listAnswer.map((qEl, idx) => {
                                        return (
                                            qEl.text
                                            ?
                                              <label key={qEl.id} data-answerid={qEl.id}>
                                                {qEl.text}
                                                <Input type="radio" onClick={() => resetInputVaule(`input${item.id}`) } name={`radio${item.id}`} id={`radio${item.id * (idx + 1)}`} />
                                              </label>
                                            :
                                            <Input key={generateKey("text")} type="text" onChange={() => resetRadioBtn(`radio${item.id}`) } placeholder="" id={`input${item.id}`} />
                                        )
                                      })
                                    }
                                </td>
                              </tr>
                            )
                          })
                        }
                        </tbody>
                      </table>
                    }
                
            </div>
            <Button type="button" onClick={() => saveAnswers()} disabled={isResultsLoading} className={`question__next-btn btn${isResultsLoading ? " disable" : ""}`}>
              {isResultsLoading ? " ..." : ""}
            </Button>
        </div>
    </div>
  )
}

export default Question