import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { AuthContext } from '../context'
import Button from './ui/Button'
import { useFetching } from '../hooks/useFetching'
import AuthService from '../api/AuthService'

const Header = () => {
  const { isAuthAdmin, setIsAuthAdmin, setAccessToken } = useContext(AuthContext)

  const logout = () => {
    localStorage.clear()
    sessionStorage.clear()
    setIsAuthAdmin(false)
    setAccessToken(null)
  }

  return (
    <div className="header">
        <div className="header__container container">
            <Link to='/' className="header__logo logo">
                 
            </Link>
            <nav className="header__menu menu">
                <div className="menu__body">
                    <ul className="menu__list">
                        <li className="menu__item">
                            <Link to='/' className="menu__link"></Link>
                        </li>
                        <li className="menu__item">
                            <Link to='/information' href="#" className="menu__link"></Link>
                        </li>
                        {
                            isAuthAdmin
                            ? 
                                <li className="menu__item">
                                    <Button type="button" className="menu__link" onClick={() => logout()}></Button>
                                </li>
                            :
                                <li className="menu__item">
                                    <Link to='/admin/login' className="menu__link"></Link>
                                </li>
                        }
                    </ul>
                </div>
            </nav>
        </div>
    </div>
  )
}

export default Header