import { useState, useEffect } from 'react'
import './assets/css/style.css'
import Header from './components/Header';
import Footer from './components/Footer';
import AppRouter from './components/AppRouter';
import { BrowserRouter } from 'react-router-dom';
import { AuthContext } from './context';

function App() {
  const [isAuthStudent, setIsAuthStudent] = useState(false);
  const [isAuthAdmin, setIsAuthAdmin] = useState(false);
  const [studentId, setStudentId] = useState(null)
  const [isLoading, setLoading] = useState(true)
  const [accessToken, setAccessToken] = useState(null)
  const [moderatorId, setModeratorId] = useState(null)

  useEffect(() => {
    document.title = " "
    if (localStorage.getItem('isAuthStudent')) {
      setIsAuthStudent(true)
      setStudentId(localStorage.getItem('studentId'))  
    } 

    if (localStorage.getItem('isAuthAdmin')) {
      setIsAuthAdmin(true)
      setAccessToken(sessionStorage.getItem('access_token'))
      setModeratorId(localStorage.getItem("moderatorId"))
    }

    setLoading(false)
  }, [])

  return (
    !isLoading &&
    <AuthContext.Provider value={{
      isAuthStudent,
      setIsAuthStudent,
      isAuthAdmin,
      setIsAuthAdmin,
      studentId,
      setStudentId,
      accessToken,
      setAccessToken,
      moderatorId,
      setModeratorId
    }}>
    <BrowserRouter>
      <div className='site-wrapper'>
        <Header />
        <main>
          <AppRouter />   
        </main>
        <Footer />
      </div>
    </BrowserRouter>
    </AuthContext.Provider>
  );
}

export default App;